@font-face {
  font-family: 'main'; /*a name to be used later*/
  src: url('./Bebas-Regular.ttf'); /*URL to font*/
}

.App {
  text-align: center;
}

.App-logo {
  position: fixed;
  height: 100%;
  left: -10px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: main;
}

a:visited {
  color: white;
}

.App-link {
  color: #61dafb;
}

.x {
  position: fixed;
  width: 100px;
  height: 100px;
}
